.navbar {
    min-height: 50px;
    background-color: var(--blue);
    display: flex;
    align-items: center;
}

.navbar .nav-links {
    display: flex;
    list-style: none;
}

.navbar .nav-links .nav-item{
    padding: 20px;
}

.navbar .nav-links .nav-item:hover{
    background-color: crimson;
}
.navbar .nav-links .nav-item:hover.nav-brand{
    background-color: transparent;
}

.navbar .nav-links .nav-item .nav-link{
    color: white;
    text-decoration: none;
}

.navbar .nav-links .nav-item.btn:hover{
    background-color: transparent;
}

.navbar .nav-links .nav-item.btn .btn-sm{
    color: white;
    text-decoration: none;
    border-radius: 6px;
    background-color: orange;
    padding: 7px 20px 8px;
}

